<template>
    <div id="menu">
        <div class="dis-cont">
            <!-- <span class="menu_logo"> -->
            <!-- logo -->
            <img
                class="menu_logo"
                src="@/assets/menu/logo.png"
                @click="$router.push('/')"
            />
            <!-- </span> -->

            <div class="btnNav" :class="{ active: isOpen }" @click="openNav()">
                <i></i>
                <i></i>
                <i></i>
            </div>
        </div>

        <div class="nav-list" :class="{ active: isOpen }">
            <ul>
                <li
                    v-for="(i, iIndex) in nav"
                    :key="iIndex"
                    class="item"
                    @click="openPage(i, iIndex, i.subNavOpen)"
                >
                    <p>{{ i.name }}</p>
                    <i
                        class="sub-nav-icon"
                        v-if="i.subNav"
                        :style="{ backgroundImage: `url(${subNavIcon})` }"
                        :class="{ active: !i.subNavOpen }"
                    ></i>
                    <div class="sub-nav" v-if="i.subNav" v-show="i.subNavOpen">
                        <p
                            v-for="(j, jIndex) in i.subNav"
                            :key="jIndex"
                            class="item"
                            @click.stop="openSubPage(i.path, jIndex)"
                        >
                            {{ j }}
                            <!-- <a href=""></a> -->
                        </p>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import subNavIcon from "@/assets/menu/nav_arrow.png";

export default {
    name: "Menu",
    data() {
        return {
            nav: [
                {
                    name: "数智风控",
                    path: "/",
                },
                {
                    name: "产品介绍",
                    path: "/about",
                },
                {
                    name: "风控案例",
                    path: "/riskControl",
                },
                {
                    name: "专项方案",
                    path: "",
                    path: "/scheme",
                    subNav: [
                        "合同履约风险",
                        "投资风险管理",
                        "采购风险管理",
                        "关联交易管理",
                        "金融领域投资",
                        "合规管理",
                        "偿二代-保险",
                    ],
                    subNavOpen: true,
                },
            ],
            // isActive: -1, // 选中
            isOpen: false, // 展开
            subNavIcon: subNavIcon,
        };
    },

    created() {},
    methods: {
        openNav() {
            this.isOpen = !this.isOpen;
        },
        openPage(item, index, subNavOpen) {
            //   console.log("-----", path, "======", index);
            // 二级菜单
            if (item.subNavOpen != undefined) {
                item.subNavOpen = !item.subNavOpen;
                return;
            }

            this.isOpen = false;
            if (this.$route.fullPath !== item.path) {
                this.$router.push(item.path);

                // 滚动到顶部
                document.body.scrollTop = 0;
                document.documentElement.scrollTop = 0;
            }
        },
        openSubPage(path, index) {
            this.isOpen = false;
            this.$router.push({
                path: path,
                query: { tabIndex: index },
            });
        },
    },
};
</script>

<style lang="scss" scoped>
#menu {
    // test:
    // opacity: 0.5;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
    .dis-cont {
        padding: 28px 40px 30px 50px;
        box-sizing: border-box;
        height: 120px;
        background: #ffffff;
        box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
    }

    .menu_logo {
        // display: inline-block;
        // img {
        width: 195px;
        // width: 100%;
        // position: absolute;
        // top: 28px;
        float: left;
        // }
    }
    .btnNav {
        width: 60px;
        height: 46px;
        float: right;
        position: relative;
        i {
            display: inline-block;
            border-radius: 20px;
            width: 40px;
            height: 5px;
            background-color: #2d73d6;
            transition: all 0.5s;
            position: absolute;
            left: 10px;
            &:nth-child(1) {
                transform-origin: left bottom;
                // transform-origin: center;
                top: 15px;
            }
            &:nth-child(2) {
                top: 28px;
            }
            &:nth-child(3) {
                top: 44px;
                transform-origin: left top;
                //  transform-origin: center;
            }
        }

        &.active {
            i:nth-child(1) {
                transform: rotate(45deg);
            }
            i:nth-child(2) {
                opacity: 0;
            }
            i:nth-child(3) {
                transform: rotate(-45deg);
            }
        }
    }

    .nav-list {
        display: none;
        background-color: #f8f9fb;

        &.active {
            display: block;
        }
        li {
            position: relative;
        }
        p {
            padding-left: 50px;
            height: 100px;
            border-bottom: 1px solid #e9e9e9;
            font-size: 30px;
            color: #000000;
            line-height: 100px;
            // 使用 no 不生效
            // https://segmentfault.com/q/1010000020854180
        }
        .sub-nav-icon {
            display: inline-block;
            width: 30px;
            height: 30px;
            position: absolute;
            right: 40px;
            top: 40px;
            // background-color: blue;
            background-size: 100%;
            transition: all 0.3s;

            &.active {
                transform: rotate(180deg);
            }
        }

        .sub-nav {
            padding-left: 50px;
        }
    }
}
</style>



