import Vue from "vue";
import Router from "vue-router";
// import HelloWorld from '@/components/HelloWorld'

Vue.use(Router);

export default new Router({
  routes: [
    // 首页
    {
      path: "/",
      name: "Home",
      component: () =>
        import(/* webpackChunkName: "home" */ "@/pages/home/HomePage")
    },
    // 产品介绍
    {
        path: '/about',
        name: 'About',
        component: () => import(/* webpackChunkName: "about" */ '@/pages/about/About')
    },
     // 风控案例
     {
        path: '/riskControl',
        name: 'RiskControl',
        component: () => import(/* webpackChunkName: "riskControl" */ '@/pages/riskControl/RiskControl')
    },

      // 专项方案-合同履约风险
      {
        path: '/scheme',
        name: 'Scheme',
        component: () => import(/* webpackChunkName: "scheme" */ '@/pages/scheme/Scheme')
    },
  ]
});



